@import url(https://fonts.googleapis.com/css?family=Roboto:regular,600);
.filter{margin:0 auto;max-width:600px;width:100%;padding:20px 20px 0;margin-bottom:20px;opacity:0.8}.filter__body{display:flex;align-items:center;border:1px solid gray;height:50px}.filter__icon{flex:0 1 50px;height:100%;display:flex;align-items:center;justify-content:center;background-color:#fff}.filter__icon svg{fill:gray;width:25px;height:25px}.filter input{width:100%;height:100%;border-left:1px solid gray;padding-left:10px;font-size:16}

.post{flex:0 1 32%;border-radius:5px;background-color:#fff;padding:20px 20px;margin:0 4px;display:flex;flex-direction:column}@media (max-width: 922px){.post{flex:0 1 48%;margin-top:15px}}@media (max-width: 600px){.post{flex:0 1 95%}}.post:not(:nth-child(-n+3)){margin-top:15px}.post__title{font-weight:400;font-size:22px;color:#65aef1;margin-bottom:10px;display:flex;align-items:center;text-transform:capitalize}.post__text{margin-bottom:10px;font-size:18px;flex:1 1 100%}.post__author{font-size:16px;color:darkgrey}

.posts{display:flex;justify-content:space-evenly;flex-wrap:wrap;padding-bottom:50px}

*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}body{font-family:Roboto}.wrapper{background-color:#d8e9f8}.container{max-width:1300px;margin:0 auto}.container:not(:last-child){margin-bottom:40px}

